import { Injectable } from '@angular/core';
import { Router } from '@angular/router';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Vendor } from './vendor';
import { environment } from 'src/environments/environment';
import { Observable } from 'rxjs';

@Injectable({
  providedIn: 'root'
})
export class AddvendorService {

  constructor(private httpService: HttpClient,private router:Router) { }

  addVendor(staff:Vendor)
  {
    let body = JSON.stringify(staff);
    let headers = new HttpHeaders({'Content-Type': 'application/json'});
    let options = { headers: headers }

    if(staff.vendorid){

      return this.httpService.put(environment.APIUrl +'/insertvendor',body,options);
    }else{
      return this.httpService.post(environment.APIUrl +'/insertvendor',body,options);
    }
  }
  goback():void{
    this.router.navigate(['/viewvendor'])
  }
  getStaffId(vendorid:number):Observable<Vendor>{
    return this.httpService.get<Vendor>(environment.APIUrl +'/api/vendor/'+ vendorid);
  }



}
