import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { AddvendorService } from '../addvendor.service';
import { Vendor } from '../vendor';

@Component({
  selector: 'app-addvendor',
  templateUrl: './addvendor.component.html',
  styleUrls: ['./addvendor.component.scss']
})
export class AddvendorComponent implements OnInit {
  staffs: Vendor[];
  staff = new Vendor();


  constructor(private route: ActivatedRoute,private _router:Router,private addstaffservice:AddvendorService) { }

  ngOnInit() {
    this.route.params.subscribe( params => this.getStaffById(params['vendorid']));
  }
  addStaff(): void{
 
    this.addstaffservice.addVendor(this.staff)
      .subscribe((response)=>{ 
        console.log(response);
        //this.reset();
        this._router.navigate(['/viewvendor']);
      }, (error)=>{

        console.log(error);
      });
  }
  getStaffById(vendorid:number){
    console.log("staff Id " +vendorid);
   this.addstaffservice.getStaffId(vendorid)
   .subscribe((searchData) =>{
      this.staff=searchData;
      console.log(searchData);
   }, (error) =>{
      console.log(error);
   });
  }
  goback():void{
    console.log("dhg")
    this._router.navigate(['/admindashboard1']);
  }

  
}
