import { Injectable } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Router } from '@angular/router';
import { Vendor } from './vendor';
import { Observable } from 'rxjs';
import { environment } from 'src/environments/environment';


@Injectable({
  providedIn: 'root'
})
export class ViewvendorService {

  constructor(private httpService: HttpClient,private router:Router) { }


  getAllVendor(): Observable<Vendor[]>{
    return this.httpService.get<Vendor[]>(environment.APIUrl +'/api/vendor');
  }

  disableAStaff(staff:Vendor){
    let body = JSON.stringify(staff);
    let headers = new HttpHeaders({'Content-Type': 'application/json'});
    let options = { headers: headers }

      return this.httpService.put(environment.APIUrl +'/disablevendor', body, options);
   }

   SearchStaff(search:string):Observable<Vendor[]>{
    return this.httpService.get<Vendor[]>(environment.APIUrl +'/api/vendor1/'+search);

  }
}
